        ElMessage(response.data.msg);
      }
      loadingInstance.close();
    };
    /**
     * 刷新Token函数
     */
    const reNew = row => {
      const loadingInstance = ElLoading.service({
        fullscreen: true
      });
      console.log(row);
      console.log(row.token);
      const api = {
        name: row.name,
        token: row.token,
        username: row.username,
        userPassword: row.userPassword,
        shared: row.shared,
        show_user_info: row.show_user_info,
        plus: row.plus,
        password: row.password
      };
      fetch("/api/updateToken", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${token}` // 确保 token 变量已定义
        },

        body: JSON.stringify(api)
      }).then(response => {
        if (!response.ok) {
          throw new Error("Network response was not ok");
        }
        return response.json();
      }).then(data => {
        if (data != null && data != "") {
          if (data.data != null) {
            row.token = data.data;
            const now = new Date();
            const formattedTime = `${now.getFullYear()}-${(now.getMonth() + 1).toString().padStart(2, "0")}-${now.getDate().toString().padStart(2, "0")} ${now.getHours().toString().padStart(2, "0")}:${now.getMinutes().toString().padStart(2, "0")}:${now.getSeconds().toString().padStart(2, "0")}`;
            row.updateTime = formattedTime;
            ElMessageBox.alert("更新成功!", "温馨提醒", {
              confirmButtonText: "OK",
              callback: () => {
                ElMessage({
                  type: "info",
                  message: "感谢Pandora大佬！"
                });
              }
            });
          } else {
            ElMessage(data.msg);
          }
        }
      }).catch(error => {
        console.error("Error:", error);
      });
      loadingInstance.close();
    };
    /**
     * 删除Token函数
     * 参数 user
     */
    const deleteToken = (index, row) => {
      const loadingInstance = ElLoading.service({
        fullscreen: true
      });
      let msg = "";
      ElMessageBox.confirm("您确定要删除这个Token吗，删除之后就找不到咯，请您要仔细认真考虑哦！", "温馨提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        axios.put(`/api/deleteToken?name=${row.name}`, null, {
          headers
        }).then(response => {
          msg = "删除成功！";
          // 从数组中移除商品项
          console.log(response.data.data);
          tableData.value.splice(index, 1);
          ElMessage({
            type: "success",
            message: msg
          });
        }).catch(error => {
          // 处理完成失败的逻辑
          console.error("删除失败", error);
        });
      }).catch(() => {
        ElMessage({
          type: "info",
          message: "删除取消！"
        });
      });
      loadingInstance.close();
    };
    /**
     * 获取token的过期时间
     */
    const formatDate = value => {
      if (!value) return "";
      var nowDay = new Date();
      const timeDay = parseISO(value);
      const daysDiff = differenceInDays(nowDay, timeDay);
      return daysDiff >= 10 ? "已经过去了至少10天" : Math.ceil(10 - daysDiff) + "天";
    };
    /**
     * 更改Token显示操作
